@font-face {
    font-family: 'G';
    src: url('./fonts/Geometria.otf'); }
@font-face {
    font-family: 'GB';
    src: url('./fonts/Geometria-Bold.otf'); }

$main: #631AEE;

* {
    box-sizing: border-box; }

body {
    background: #f3f3f3 url(./img/bg.jpg) no-repeat center / cover;
    font-family: G;
    width: 100%;
    min-height: 100vh;
    margin: 0;
    padding: 0;
    padding-top: 100px; }
input,
select,
label,
textarea,
button {
    font-family: G; }

.part {
    margin: auto;
    max-width: 1200px;
    display: flex; }

.topbar {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0 0 0 20px;
    padding: 10px 15px 10px 30px;
    background: linear-gradient(0deg, #ccc, #f3f3f3);
    .tb__item {
        font-size: 0.7rem; } }

.col-wrapper {
    margin: 5px;
    &[width="120"] {
        flex-basis: 120%; }
    &[width="100"] {
        flex-basis: 100%; }
    &[width="90"] {
        flex-basis: 90%; }
    &[width="80"] {
        flex-basis: 80%; }
    &[width="50"] {
        flex-basis: 50%; }
    &[width="30"] {
        flex-basis: 30%;
        .th {
            font-size: 0.7rem; }
        .options {
            min-width: auto; } } }

.cell-option {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 5px;
    width: 25px;
    height: 25px; }

.color-title-counter {
    font-size: 0.9rem; }

.th {
    text-align: center;
    background-color: $main;
    border-radius: 10px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-family: GB;
    span {
        width: 100%; }
    .options {
        display: flex;
        justify-content: center;
        padding: 5px;
        font-size: 22px;
        position: relative;
        min-width: 45px;
        .menu {
            position: absolute;
            top: calc(100% + 5px);
            right: 0;
            background: #f3f3f3;
            color: #333;
            z-index: 1;
            font-size: 14px;
            min-width: 200px;
            padding: 15px 0 3px 0;
            border: 1px solid #dddddd;
            border-radius: 8px;
            display: none;
            text-align: left;
            white-space: nowrap;
            .menu-item {
                margin-bottom: 15px;
                padding: 0 15px;
                cursor: pointer;
                display: flex;
                align-items: center;
                font-family: G;
                i {
                    margin-right: 10px;
                    font-size: 0.9rem; }
                &:hover>span {
                    text-decoration: underline; }
                &.has-children {
                    position: relative;
                    &::after {
                        content: '';
                        width: 8px;
                        height: 8px;
                        position: absolute;
                        top: calc(50% - 4px);
                        right: -4px;
                        background-color: $main;
                        border-radius: 50%; }
                    &:hover {
                        .sub-menu {
                            display: block; } } }
                .sub-menu {
                    position: absolute;
                    left: calc(100%);
                    top: -15px;
                    display: none;
                    min-width: fit-content;
                    .menu-item {
                        text-align: center; } } } }
        .delimeter {
            width: 100%;
            height: 1px;
            background-color: #ccc;
            margin-bottom: 15px; }
        &.open .menu {
            display: block; }
        i {
            cursor: pointer;
            opacity: .85;
            transition: all .3s;
            &:hover {
                opacity: 1; } } } }

.replace-form {
    position: fixed;
    top: 5px;
    right: 5px;
    background: #f3f3f3;
    padding: 5px;
    border-radius: 5px;
    font-size: 12px;
    border: 1px solid #ddd;
    .d-flex {
        align-items: center;
        justify-content: space-between; }
    input {
        border: 1px solid #ddd;
        padding: 8px;
        margin: 5px; } }

.col-changer-bar {
    width: 100%;
    display: flex;
    .col-icon {
        display: flex;
        width: 100%;
        margin: 5px;
        box-sizing: border-box;
        height: 15px;
        border-radius: 3px;
        border: 1px solid #999;
        background-color: #dfdfdf;
        cursor: pointer;
        &.changed {
            background-color: #999; } } }

input.find-word {
    border: 1px solid red; }

.cell-wrapper {
    display: flex;
    align-items: center;
    position: relative;
    label {
        width: 100%;
        position: relative; }
    &[disabled] {
        label,i {
            opacity: .4; } }
    &:nth-child(2) {
        margin-top: 10px; }
    & i {
        font-size: 20px;
        cursor: pointer; } }

.before {
    display: flex;
    align-items: center;
    position: absolute;
    right: calc(100% + 7px); }

.row-number,.add-row {
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
    border: 1px solid #ececec;
    background-color: #f3f3f3;
    border-radius: 3px;
    color: #555;
    margin: 0 5px; }

.row-number.disabled:after {
    content: "";
    position: absolute;
    height: 2px;
    width: 100%;
    background-color: red;
    transform: rotate(135deg); }

.add-row {
    margin-left: 5px;
    margin-top: 10px;
    font-size: 18px;
    line-height: 0;
    cursor: pointer; }

i.bi-x {
    color: red;
    font-weight: bolder; }
i.bi {
    color: $main; }

.cell-input {
    padding: 7px;
    border: 1px solid #ddd;
    margin: 3px 0;
    border-radius: 5px;
    background-color: #fff;
    font-size: 0.9rem;
    width: 100%; }

.selected-marker {
    position: absolute;
    right: -2px;
    bottom: 1px;
    width: 8px;
    height: 8px;
    background-color: #000;
    cursor: pointer; }

.table {
    display: flex;
    justify-content: center;
    padding-left: 30px; }

.before {
    i {
        transform: all 0.3s;
        margin: 0 5px;
        color: $main;
        &:hover {
            color: $main; } } }

.btn {
    border: 1px outset #ddd;
    text-transform: uppercase;
    font-size: 10px;
    padding: 10px 5px;
    text-align: center;
    background-color: #fff;
    cursor: pointer;
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    color: #333;
    transition: all .3s;
    &:hover {
        background-color: #f9f9f9; }
    &.changed {
        background-color: $main;
        color: #fff; }
    .badge {
        border: 1px solid #333;
        min-width: 17px;
        width: fit-content;
        display: block;
        border-radius: 50px;
        padding: 1px 3px;
        position: absolute;
        bottom: -10px;
        left: -7px;
        background-color: #fff; }
    &.icon {
        font-size: 2rem;
        line-height: 1;
        height: 45px; } }

.sidebar {
    width: 130px;
    padding: 15px;
    .delimeter {
        border-top: 2px solid #999;
        margin: 15px auto;
        width: 80%; }
    .widget {
        position: relative;
        margin-bottom: 15px;
        .sidebar-submenu {
            background-color: #f3f3f3;
            border: 1px solid #ddd;
            border-radius: 3px;
            min-width: 40px;
            padding: 5px;
            position: absolute;
            right: 105%;
            top: 0;
            display: flex;
            align-items: center;
            flex-direction: column; } } }

.faq {
    margin-top: 40px;
    display: block;
    padding: 0;
    font-size: 0.9rem;
    li {
        margin: 7px; } }

.spellchecker-error-indicator {
    position: absolute;
    right: 3px;
    top: 2px;
    i {
        color: red;
        font-size: 14px; } }

.updown-bar {
    position: fixed;
    right: 30px;
    bottom: 30px;
    .btn {
        margin: 5px; } }

.context-menu {
    position: absolute;
    top: 50%;
    left: 50%;
    &__item {
        margin-bottom: 15px;
        padding: 0 15px;
        cursor: pointer;
        display: flex;
        align-items: center;
        font-family: G;
        i {
            margin-right: 10px;
            font-size: 0.9rem; }
        &:hover>span {
            text-decoration: underline; } } }

.card {
    background: #f3f3f3;
    color: #333;
    z-index: 1;
    font-size: 14px;
    min-width: 200px;
    padding: 15px 0 3px 0;
    border: 1px solid #dddddd;
    border-radius: 8px;
    text-align: left;
    white-space: nowrap;
    overflow: hidden; }
.card-header {
    background-color: $main;
    color: #fff;
    font-weight: bold; }
.card-footer {
    border-top: 1px solid #ddd;
    padding: 10px 0;
    .context-menu__item {
        width: 100%;
        margin-bottom: 0; } }

.bbufer {
    position: fixed;
    right: 0;
    top: 50%;
    &.card {
        padding: 0; }
    .card-header,.card-body,.card-footer {
        padding: 10px; }
    .card-body {
        background-color: #fff; }
    &.close {
        background-color: $main;
        color: #fff;
        font-family: GB;
        width: 45px;
        height: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 7px 0 0 7px;
        cursor: pointer; } }

.synch {
    position: fixed;
    top: 10px;
    left: 10px;
    font-size: 25px; }

//====================== UTILITES
.mr5 {
    margin-right: 5px; }
.d-flex {
    display: flex; }
.line-through {
    text-decoration: line-through; }
.pointer {
    cursor: pointer; }
.tac {
    text-align: center; }

//======================= ANIMATION
.spin, .bi.spin:before {
    animation: spin 2s linear infinite; }

@keyframes spin {
    0% {
        transform: rotate(0deg); }
    100% {
        transform: rotate(360deg); } }
